<script setup>
import { computed } from "vue";
import {
  mdiCashMinus,
  mdiCashPlus,
  mdiReceipt,
  mdiCreditCardOutline,
} from "@mdi/js";
import CardBox from "@/components/CardBox.vue";
import BaseLevel from "@/components/BaseLevel.vue";
import PillTag from "@/components/PillTag.vue";
import IconRounded from "@/components/IconRounded.vue";

const props = defineProps({
  project: {
    type: Number,
    required: true,
  },
  tagline: {
    type: String,
    required: true,
  },
  sector: {
    type: String,
    required: true,
  },
  ticker: {
    type: String,
    required: true,
  },
  logo: {
    type: String,
    required: true,
  },
});

const icon = computed(() => {
  if (props.sector === "Smart Contract Platforms") {
    return {
      type: "info",
    };
  } else if (props.sector === "Decentralized Exchange") {
    return {
      type: "success",
    };
  } else if (props.sector === "Currencies") {
    return {
      type: "warning",
    };
  }
  return {
    type: "danger",
  };
});
</script>

<template>
  <CardBox class="mb-6 last:mb-0" is-hoverable>
    <BaseLevel>
      <BaseLevel type="justify-start">
        <img :src="logo" class="w-10 h-10 rounded-full">
        <div class="px-4 space-y-1 md:text-left md:mr-6">
          <h4 class="text-xl">{{ project }}</h4>
          <p class="text-gray-500 dark:text-slate-400">
            {{ tagline }}
          </p>
        </div>
      </BaseLevel>
      <div class="text-center md:text-right space-y-2">
        <p class="text-sm text-gray-500">
          {{ ticker }}
        </p>
        <div>
          <PillTag :color="icon.type" :label="sector" small />
        </div>
      </div>
    </BaseLevel>
  </CardBox>
</template>
